import React from 'react';
import {connect} from "react-redux"

const reviewForm=(props)=>{
    const street_address = props.data.stepperFormData.addressData[0].long_name + props.data.stepperFormData.addressData[1].long_name
    const city = props.data.stepperFormData.addressData[2].long_name
    const country = props.data.stepperFormData.addressData[6].long_name
    const province = props.data.stepperFormData.addressData[5].long_name
     
    const {name, landlord_review}=props.data.stepperFormData.landlord_bio
return(
  <div>
    <h3>Summary</h3>
    <hr />
    <p><span className="label">Street Address:</span>{street_address}</p>
    <p><span className="label">City:</span>{city}</p>
    <p><span className="label">Province:</span>{province}</p>
    <p><span className="label">Country:</span>{country}</p>
    <p><span className="label">Name: </span>{name}</p>
    <p><span className="label">Bio:</span>{landlord_review}</p>
    <style jsx>{`
    p {
      color: #FF5941;
    };

    .label {
      color: #fff;
      width: 200px; 
      margin-left: 10px
    };

    h3 {
      margin: 0;
      padding:0;
      text-align: center;
      font-family: poppin, san-serf
    };
    hr {
      background-color: #FF5941;
      padding: 1px;
      border: none
    }
    `}</style>
  </div>
)}

const mapStateToProps=(state)=>{
  return{
    data: state.app
  }
}
export default connect(mapStateToProps)(reviewForm)